import "./MovieType.scss";

export const MovieType = ({ movieType }: { movieType: string }) => {
  return (
    <span className="movie-type">
      {movieType === "movie"
        ? ""
        : movieType === "cartoon"
        ? ""
        : movieType === "tv-series"
        ? ""
        : movieType === "anime"
        ? ""
        : "-"}
    </span>
  );
};
